@import "../../global.scss";

.menu{
    width: 300px;
    height: 100vh;
    background-color: $mainColor;
    position: fixed;
    margin-top: 0px;
    right: -300px;
    z-index: 2;//initially the z-index was 1, am changing it to 2
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: all 1s ease;

    @include mobile{
        width: 200px;
        margin-top: 0px;
        right: -300px;
        
    }
    

    &.active{
        right: 0;
    }
    ul{
        margin-top: 0;
        padding: 0;
        list-style: none;
        font-size: 30px;
        color: white;
        font-weight: 300px;
        width: 60%;

        li{
            margin-bottom: 20px;
            a{
                font-size: inherit;
                color: inherit;
                text-decoration: none;
            }
            &:hover{
                font-size: 35px;
            }
           
        }
        
    }
    
}