@import "../../global.scss";

.topbar{
    width: 100%;
    height: 70px;
    background-color: white;
    color: $mainColor;
    position: fixed;
    margin-top: 0;
    z-index: 3;//initially the z-indez was 2
    transition: all 1s ease;
    
    .wrapper{
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;



        .left{
            display: flex;
            align-items: center;
            justify-content: center;
            .logo{
                font-size: 40px;
                font-weight: 700;
                text-decoration: none;
                color: inherit;
            }
            .icon{
                display: flex;
                align-items: center;
                justify-content: center;
                margin-left: 5px;


                @include mobile{
                    display: none;
                }

                img{
                    height: 15px;
                    width: 15px;
                    padding: 5px;
                   
                    
                }
                span{
                    font-size: 14px;
                    color: $mainColor;
                }
            }

           

        }
        .right{
            
            .hamburger{
                width: 32px;
                height: 25px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                cursor: pointer;
                

                span{
                    width: 100%;
                    height: 3px;
                    background-color: $mainColor;
                    transform-origin: left;
                    transition: all 2s ease;
                }
            }
        }
    }
    &.active{
        background-color: $mainColor;
        color: white;
        

        .hamburger{
           span{
            
            &:first-child{
                background-color: white;
                transform: rotate(45deg);
                
            }
            &:nth-child(2) {
                opacity: 0;
            }
        
            &:last-child{
                background-color: white;
                transform: rotate(-45deg);
                
            }
        }
           }
    }
}